import React, { Suspense, lazy, useEffect, useState } from 'react'
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import { useCurrentTimex10 } from 'hooks/useTimer'
import BigNumber from 'bignumber.js'
import { Helmet } from 'react-helmet'
import { useFetchPublicData, usePriceDollar, usePriceShare } from './state/hooks'
import GlobalStyle from './style/Global'
import Menu from './components/Menu'
import PageLoader from './components/PageLoader'
import ResetCSS from './ResetCSS'

const Home = lazy(() => import('./views/Home'))
const Farms = lazy(() => import('./views/Farms'))
const Boardroom = lazy(() => import('./views/Boardroom'))
const Bonds = lazy(() => import('./views/Bonds'))
const NotFound = lazy(() => import('./views/NotFound'))

// This config is required for number formating
BigNumber.config({
  EXPONENTIAL_AT: 1000,
  DECIMAL_PLACES: 80,
})

const App: React.FC = () => {
  const [value, setValue] = useState('Supra Dollar')
  const [x, setX] = useState(false)
  const { account, connect } = useWallet()
  const price = usePriceDollar()
  const priceShare = usePriceShare()
  const block = useCurrentTimex10()

  useEffect(() => {
    setValue(x ? `Supra Dollar - SDO $${price.toFixed(3)}` : `Supra Dollar - SSO $${priceShare.toFixed(3)}`)
    setX(!x)
    /* eslint react-hooks/exhaustive-deps: 0 */
  }, [block])

  useEffect(() => {
    if (!account && window.localStorage.getItem('accountStatus')) {
      connect('injected')
    }
  }, [account, connect])

  useFetchPublicData()

  return (
    <Router>
      <ResetCSS />
      <GlobalStyle />
      <Menu>
        <Suspense fallback={<PageLoader />}>
          <Switch>
            <Route path="/" exact>
              <Helmet>
                <title>{value}</title>
              </Helmet>
              <Home />
            </Route>
            <Route path="/banks">
              <Farms isSSO={false} />
            </Route>
            <Route path="/shares">
              <Farms isSSO />
            </Route>
            <Route path="/boardroom">
              <Boardroom />
            </Route>
            <Route path="/bonds">
              <Bonds />
            </Route>
            <Route component={NotFound} />
          </Switch>
        </Suspense>
      </Menu>
    </Router>
  )
}

export default React.memo(App)
