import BigNumber from 'bignumber.js'
import { useEffect } from 'react'
import { useSelector, useDispatch } from 'react-redux'
import { useTotalState } from 'hooks/useHarvestFromBoardroom'
import useRefresh from 'hooks/useRefresh'
import { State, Farm } from './types'
import { FarmType, QuoteToken } from '../config/constants/types'
import { fetchFarmsPublicDataAsync } from './farms'

const ZERO = new BigNumber(0)

export const useFetchPublicData = () => {
  const dispatch = useDispatch()
  const { slowRefresh } = useRefresh()
  useEffect(() => {
    dispatch(fetchFarmsPublicDataAsync())
  }, [dispatch, slowRefresh])
}

// Farms

export const useFarms = (): Farm[] => {
  const farms = useSelector((state: State) => state.farms.data)
  return farms
}

export const useFarmFromPid = (pid): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.pid === pid))
  return farm
}

export const useFarmFromSymbol = (lpSymbol: string): Farm => {
  const farm = useSelector((state: State) => state.farms.data.find((f) => f.lpSymbol === lpSymbol))
  return farm
}

export const useFarmUser = (pid) => {
  const farm = useFarmFromPid(pid)

  return {
    allowance: farm.userData ? new BigNumber(farm.userData.allowance) : new BigNumber(0),
    tokenBalance: farm.userData ? new BigNumber(farm.userData.tokenBalance) : new BigNumber(0),
    stakedBalance: farm.userData ? new BigNumber(farm.userData.stakedBalance) : new BigNumber(0),
    earnings: farm.userData ? new BigNumber(farm.userData.earnings) : new BigNumber(0),
  }
}

export const usePriceBnbBusd = (): BigNumber => {
  const pid = 999 // BUSD-BNB LP
  const farm = useFarmFromPid(pid)
  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
}

export const usePriceDollar = (): BigNumber => {
  const pid = 10
  const farm = useFarmFromPid(pid)
  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
}

export const usePriceShare = (): BigNumber => {
  const pid = 24
  const farm = useFarmFromPid(pid)
  return farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
}

export const usePriceFromPid = (pid): BigNumber => {
  const farm = useFarmFromPid(pid)
  const bnb = usePriceBnbBusd()
  const value = farm.tokenPriceVsQuote ? new BigNumber(farm.tokenPriceVsQuote) : ZERO
  return farm.tokenSymbol === QuoteToken.BNB ? bnb.div(value) : value
}

export const usePriceBond = (): BigNumber => {
  const dollar = usePriceDollar()
  return dollar // TODO: Multiply by premium rate
}

export type PricesQuoteToken = {
  [QuoteToken.BNB]: BigNumber
  [QuoteToken.SDO]: BigNumber
  [QuoteToken.SSO]: BigNumber
  [QuoteToken.SLME]: BigNumber
  [QuoteToken.BDO]: BigNumber
  [QuoteToken.MDO]: BigNumber
  [QuoteToken.BTD]: BigNumber
  [QuoteToken.SPG]: BigNumber
  [QuoteToken.CRL]: BigNumber
  [QuoteToken.VLAD]: BigNumber
  [QuoteToken.CAKE]: BigNumber
  [QuoteToken.ALLOY]: BigNumber
}

export const calculateTotal = (
  { isTokenOnly, tokenAmount, lpTotalInQuoteToken, quoteTokenSymbol }: Farm,
  prices: PricesQuoteToken,
): BigNumber => {
  const base = new BigNumber((isTokenOnly ? tokenAmount : lpTotalInQuoteToken) || 0)
  const price = prices[quoteTokenSymbol] ?? new BigNumber(1)
  return base.multipliedBy(price)
}

export const usePrices = () => {
  const bnbPrice = usePriceBnbBusd()
  const dollarPrice = usePriceDollar()
  const sharePrice = usePriceShare()
  const spgPrice = usePriceFromPid(17)
  const alloyPrice = usePriceFromPid(991)
  const cakePrice = usePriceFromPid(992)
  const vladPrice = usePriceFromPid(993)
  const crlPrice = usePriceFromPid(994)
  const btdPrice = usePriceFromPid(995)
  const slimePrice = usePriceFromPid(996)
  const bdoPrice = usePriceFromPid(997)
  const mdoPrice = usePriceFromPid(998)

  const prices: PricesQuoteToken = {
    [QuoteToken.BNB]: bnbPrice,
    [QuoteToken.SDO]: dollarPrice,
    [QuoteToken.SSO]: sharePrice,
    [QuoteToken.SLME]: slimePrice,
    [QuoteToken.BDO]: bdoPrice,
    [QuoteToken.MDO]: mdoPrice,
    [QuoteToken.BTD]: btdPrice,
    [QuoteToken.SPG]: spgPrice,
    [QuoteToken.CRL]: crlPrice,
    [QuoteToken.VLAD]: vladPrice,
    [QuoteToken.CAKE]: cakePrice,
    [QuoteToken.ALLOY]: alloyPrice,
  }

  return prices
}

export const useTotalValue = (): BigNumber => {
  const farms = useFarms()
  const prices = usePrices()
  const totalSupply = useTotalState()
  const price = totalSupply ? totalSupply.times(prices.SSO) : ZERO

  return farms
    .filter((farm) => farm.type !== FarmType.SERVICE && farm.lpTotalInQuoteToken)
    .reduce((value, farm) => value.plus(calculateTotal(farm, prices)), price)
}
